<template>
    <section>
        <div class="row">
            <div class="col-md-12" v-if="!isExcelFile">
                <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Billing" :listType="['List']"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                    SubModuleCode="Billing" IdentityColumn="WORK_CODE_CATEGORY_ID" :HeaderText="$t('Category')"
                    :ListData="WorkCodeCategoryData" :HeaderData="Headers" :callbackfunction="FetchData"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                    :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy"
                    :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:SortdataButtonClick="sortdata">
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'" class="single-action">
                            <span v-if="data.row.STATUS_ID == 1001">
                                <em class="fa fa-check text-success fa-2x" style="color: #000000;"></em>
                            </span>
                            <span v-else>
                                <em class=" fa fa-times text-danger fa-2x"></em>
                            </span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'CREATE_DATE'">
                            <span>{{ $d(data.row.CREATE_DATE) }}</span>
                        </template>

                    </template>
                </tg-list>

            </div>
        </div>


        <div class="my-popups" v-if="showAddModel">
            <div class="modal d-block dialog">
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">{{ $t('Manage Rate Code Category') }}</h4>
                            <button type="button" class="close" @click="close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body p-4">
                            <div class="d-flex justify-content-center">
                                <div class="w-100" style="max-width: 900px;">
                                    <dynamic-form :schema="FormSchema" :buttons="buttons"
                                        v-slot:tgslot-Content="{ data }">
                                        <template>

                                        </template>
                                        <div class="form-group">
                                            <ckeditor :editor="editor" v-model="editorData">
                                            </ckeditor>
                                            <span class="invalid-feedback d-block">{{ data.error }}</span>
                                        </div>
                                    </dynamic-form>

                                    <div class="text-left text-danger mt-2">
                                        {{ $t('MandatoryString') }}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <AddCategory v-if="isEditCategory" :callbackfunction="FetchData" :editDetail="EditPlanDetail"
            @CloseCategory="CloseCategory" :refCode="refCode">
        </AddCategory>
    </section>
</template>


<script>
import Schema from '../Category/CategorySchema';
import DataService from '../../../services/DataService';
import AddCategory from '../Category/AddCategory';
export default {
    components: { AddCategory },
    data() {
        return {
            dateformat: null,
            isEditCategory: false,
            isExcelFile: false,
            ExcelPanel: [],
            editor: this.CLASSIC_EDITOR,
            editorData: '',
            recordId: 0,
            noRecord: false,
            isLoading: true,
            WorkCodeCategoryData: [],
            filteredHeaders: [],
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: Schema.leftsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            searchCondition: '',
            conditionForInstantSearch: '',
            SortBy: '',
            SortExp: '',
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            MODULENAME: 'Company',
            SUBMODULECODE: 'EmailTemplate',
            privilegeParams: null,
            refCode: '',
            EditPlanDetail: [],
            Headers: [
                {
                    COLUMN_NAME: "WORK_CODE_CATEGORY",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('RateCodeCategory'),
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "WORK_CODE_CATEGORY_DESCRIPTION",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('DESCRIPTION'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "CREATED_BY_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CREATED_BY'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "CREATE_DATE",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedDate'),
                    DISPLAY_ORDER: 4,
                },

                {
                    COLUMN_NAME: "STATUS_ID",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Status'),
                    DISPLAY_ORDER: 5,
                }

            ],
            Item: [],
            showAddModel: false,

        }
    },
    async created() {

        var ths = this;
        ths.FetchData();
    },
    methods: {
        FetchData: async function () {
            debugger
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&search=${vueObj.searchCondition}`;
            await DataService.GetWorkCodeCategoryList(url).then((response) => {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'STATUS_ID' || column.COLUMN_NAME == 'CREATE_DATE') {
                        column.settings = {
                            isInSlot: true
                        }
                    }

                    if (column.COLUMN_NAME === vueObj.SortBy) {
                        column.SortExp = vueObj.SortExp;
                    }
                });
                debugger
                if (response.data != null && response.data != '') {
                    var result = JSON.parse(response.data);
                    result.forEach(element => {
                        if (element.CREATE_DATE) {
                            element.CREATE_DATE = new Date(element.CREATE_DATE);
                        }
                    })
                    vueObj.WorkCodeCategoryData = result;
                    vueObj.TotalRecords = result[0].TOTAL;
                    vueObj.noRecord = false;
                }
                else {
                    vueObj.WorkCodeCategoryData = [];
                    vueObj.TotalRecords = 0;
                    vueObj.noRecord = true;
                }

            });
            // vueObj.noRecord = vueObj.TotalRecords === 0;
            vueObj.isLoading = false;
        },

        AddWorkRecord: function () {
            var vm = this;
            vm.refCode = '',
                vm.isEditCategory = true;
        },
        CloseCategory() {
            debugger
            this.isEditCategory = false;
            this.FetchData();
        },
        triggerEditReport() {
            this.CloseCategory();
        },
        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'EditRecord':
                    this.EditCategory(item);
                    break;
            }
        },
        Download: async function () {
            var vm = this;
            var url = `pageSize=${10}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&search=${vm.searchCondition}`;
            const obj = JSON.stringify(url);
            var requestPayload = { objData: obj };
            await DataService.GetWorkCodeCategoryList(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.length > 0) {
                        vm.ConvertJSONToCSV(response.data, "Category", true);
                    }
                }
            })
        },
        ConvertJSONToCSV: function (jsonData, filename, showHeader) {
            debugger;
            const data = JSON.parse(jsonData);
            const excludeColumns = ['template_id', 'STATUS_ID', 'TOTAL', 'RCOUNT', 'WORK_CODE_CATEGORY_ID'];
            const headerMap = {
                'ROWNUMBER': 'Sr No',
                'WORK_CODE_CATEGORY': 'Rate Code Category', 'WORK_CODE_CATEGORY_DESCRIPTION': 'Description', 'CREATED_BY_NAME': 'Create By Name', 'CREATE_DATE': '	Create Date',
                'STATUS_NAME': 'Status',
            };
            if (!Array.isArray(data) || data.length === 0) {
                console.warn("No valid data to convert.");
                return;
            }
            if (data.length > 0) {
                const headers = Object.keys(data[0]);
                const filteredHeaders = headers.filter(header => !excludeColumns.includes(header));
                const mappedHeaders = filteredHeaders.map(header => headerMap[header] || header);
                const rows = data.map(row => {
                    return mappedHeaders.map(mappedHeader => {
                        const originalHeader = filteredHeaders[mappedHeaders.indexOf(mappedHeader)];
                        let cellValue = row[originalHeader] !== undefined && row[originalHeader] !== null ? String(row[originalHeader]) : '';
                        if (cellValue.includes(',') || cellValue.includes('\n') || cellValue.includes('"')) {
                            cellValue = `"${cellValue.replace(/"/g, '""')}"`;
                        }

                        return cellValue;
                    }).join(',');
                });
                debugger
                const csvContent = (showHeader ? [mappedHeaders.join(',')] : []).concat(rows).join('\n');
                const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
                const link = document.createElement('a');
                link.setAttribute('href', URL.createObjectURL(blob));
                link.setAttribute('download', `${filename}.csv`);
                link.click();
            } else {
                console.log("No valid data to convert.");
            }
        },
        EditCategory(item) {
            var vm = this;
            vm.refCode = 'Update',
                vm.EditPlanDetail = item;
            vm.isEditCategory = true;

        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;
                case "AddWorkRecord":
                    this.AddWorkRecord();
                    break;
                case "ExportToExcel":
                    this.Download();
                    break;
            }
        },

        Download: async function () {
            var vm = this;
            var url = `pageSize=${10}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&search=${vm.searchCondition}`;
            const obj = JSON.stringify(url);
            var requestPayload = { objData: obj };
            await DataService.GetWorkCodeCategoryList(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.length > 0) {
                        vm.ConvertJSONToCSV(response.data, "Category", true);
                    }
                }
            })
        },
        ConvertJSONToCSV: function (jsonData, filename, showHeader) {
            debugger;
            const data = JSON.parse(jsonData);
            const excludeColumns = ['template_id', 'STATUS_ID', 'TOTAL', 'RCOUNT', 'WORK_CODE_CATEGORY_ID'];
            const headerMap = {
                'ROWNUMBER': 'Sr No',
                'WORK_CODE_CATEGORY': 'Rate Code Category', 'WORK_CODE_CATEGORY_DESCRIPTION': 'Description', 'CREATED_BY_NAME': 'Create By Name', 'CREATE_DATE': '	Create Date',
                'STATUS_NAME': 'Status',
            };
            if (!Array.isArray(data) || data.length === 0) {
                console.warn("No valid data to convert.");
                return;
            }
            if (data.length > 0) {
                const headers = Object.keys(data[0]);
                const filteredHeaders = headers.filter(header => !excludeColumns.includes(header));
                const mappedHeaders = filteredHeaders.map(header => headerMap[header] || header);
                const rows = data.map(row => {
                    return mappedHeaders.map(mappedHeader => {
                        const originalHeader = filteredHeaders[mappedHeaders.indexOf(mappedHeader)];
                        let cellValue = row[originalHeader] !== undefined && row[originalHeader] !== null ? String(row[originalHeader]) : '';
                        if (cellValue.includes(',') || cellValue.includes('\n') || cellValue.includes('"')) {
                            cellValue = `"${cellValue.replace(/"/g, '""')}"`;
                        }

                        return cellValue;
                    }).join(',');
                });
                debugger
                const csvContent = (showHeader ? [mappedHeaders.join(',')] : []).concat(rows).join('\n');
                const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
                const link = document.createElement('a');
                link.setAttribute('href', URL.createObjectURL(blob));
                link.setAttribute('download', `${filename}.csv`);
                link.click();
            } else {
                console.log("No valid data to convert.");
            }
        },

        commonDeleteData: function (event) {
            debugger
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_CATEGORY"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    debugger
                    DataService.CommonCommonDelete(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Category')]), "success", true, vm.$t("Alert"));
                            await vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Category')]), "failure", true, vm.$t("Alert"));
                        }

                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            }

        },
        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001;
            } else {
                status = 1002;
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.isLoading = true;
                vm.listheaderbuttons[1].isdisabled = false;
                vm.listheaderbuttons[2].isdisabled = false;
                // vm.listheaderbuttons[3].isdisabled = false;
                vm.listheaderbuttons[4].isdisabled = false;
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "TALYGEN_CATEGORY",
                        Module: "BILLING",
                        Submodule: "TALYGEN_CATEGORY",
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonUpdateStatus(objectPostString).then(async response => {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.listheaderbuttons[1].isdisabled = true;
                            vm.listheaderbuttons[2].isdisabled = true;
                            // vm.listheaderbuttons[3].isdisabled = true;
                            vm.listheaderbuttons[4].isdisabled = true;
                            await vm.FetchData();
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));

                        }
                    });

                });
            }

        },
        pagerMethod(value) {
            debugger;
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                const match = value.searchCondition.match(/'%(.+?)%'/);
                var searchValue = ''
                if (match) {
                    searchValue = match[1];
                }
                this.searchCondition = searchValue;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        }
    },
    getStatusClass: function (status) {
        var returnClass = '';
        switch (status) {
            case 'Active':
                returnClass = 'fa fa-check text-black';
                break;
            case 'Inactive':
                returnClass = 'fa fa-check text-muted';
                break;
            default:
                returnClass = '';
        }
        return returnClass;
    },


}
</script>

<style></style>