<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block" v-if="isEditCategory">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">
                                {{ refCode === 'Update' ? $t('UpdateCategory') : $t('AddCategory') }}
                            </h5>
                            <button type="button" class="close" @click="CloseCategory">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validatePlan" :buttons="buttons" :schema="FormSchema"
                                :mandatorytext="$t('MandatoryString')" />

                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>


<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        callbackfunction: {
            type: Function,
            required: false
        },
        refCode: {
            type: String,
            required: false
        },
        editDetail: {
            type: Function,
            required: false
        }
    },
    data() {
        return {
            isLoading: false,
            isEditCategory: true,
            category_Id: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('RateCodeCategory'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('RateCodeCategory'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options: [{ value: "1001", name: this.$t("Active") }, { value: "1002", name: this.$t("Inactive") }]
                            },
                            validationRules: {
                                required: true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('DESCRIPTION'),
                            validationRules: { required: true, max: 6000 },
                        }
                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseCategory,
                },
            ],
        }
    },
    mounted() {
        if (this.editDetail && Object.keys(this.editDetail).length > 0 && this.refCode == 'Update') {
            this.BindEditDetail(this.editDetail);
        }
    },
    methods: {
        async BindEditDetail(editDetail) {
            var vm = this;
            vm.category_Id = editDetail.WORK_CODE_CATEGORY_ID;
            vm.$refs.validatePlan.updateProperty("category", "value", editDetail.WORK_CODE_CATEGORY);
            vm.$refs.validatePlan.updateProperty("Status", "value", editDetail.STATUS_ID,);
            vm.$refs.validatePlan.updateProperty("Description", "value", editDetail.WORK_CODE_CATEGORY_DESCRIPTION);
        },
        async SaveData() {
            var vm = this;
            vm.isLoading = true;
            const result = await vm.$refs.validatePlan.validateForm.validate();
            if (!result) {
                this.isLoading = false;
                return;
            }
            const workCode = vm.$refs.validatePlan.getPropertyValue("category", "value");
            const status = vm.$refs.validatePlan.getPropertyValue("Status", "value");
            const description = vm.$refs.validatePlan.getPropertyValue("Description", "value");

            var objData = {
                id: vm.category_Id,
                category_name: workCode,
                category_description: description,
                status: status,
            }
            var jsonconvert = JSON.stringify(objData);
            var url = `name=${workCode}`
            if (!vm.category_Id) {
                var existResponse = await DataService.IsExistCategory(url);

                if (existResponse.data.message === "Exist") {
                    vm.isLoading = false;
                    this.ShowAlert(this.$t('AlreadyExists', [this.$t('Category')]), "failure", true, this.$t('Alert'));
                    return;
                }
            }
            await DataService.CreateWorkCodeCategory(jsonconvert).then(async response => {
                debugger
                if (response.data == 1) {
                    this.ShowAlert(this.$t('AddedSuccess', [this.$t('Category')]), "Success", true, this.$t('Alert'));
                }
                else {
                    this.ShowAlert(this.$t('UpdatedSuccess', [this.$t('Category')]), "Success", true, this.$t('Alert'));
                }
                this.CloseCategory();
            });

        },
        CloseCategory() {
            this.$emit('CloseCategory');
        }
    }
}
</script>

<style lang="scss" scoped></style>